import { Component } from '@angular/core';

@Component({
  selector: 'app-root',
  template: `
    <ngx-monaco-editor [options]="options" [(ngModel)]="code"></ngx-monaco-editor>
  `,
  styles: []
})
export class AppComponent {
  options = {
    language: 'javascript',
    theme: 'vs-dark'
  };
  title = 'app';
  code = `// Hello Anhnt`;

  updateOptions() {
    this.options = Object.assign({}, this.options, { language: 'css' });
  }
}



